module.exports = "`ui-list` 组件的触摸功能（类似微信的侧滑删除） <strong class=\"color-danger\">未做PC端兼容</strong>，只有在模拟器或手机上才有效果，你可以点击右上角的按扭，新开一个窗口，在模拟器中查看。\n\n\n列表是由一组组件，通过不同的嵌套组合，即可实现 native 中常见的列表布局样式，其中，最基本的结构为：\n\n```html\n<!--普通列表-->\n<ui-list>\n  <ui-list-item>\n    <!--任意内容-->\n  </ui-list-item>\n  <ui-list-item>\n    <!--任意内容-->\n  </ui-list-item>\n  <ui-list-item>\n    <!--任意内容-->\n  </ui-list-item>\n</ui-list>\n\n<!--带箭头和点击高亮效果的列表-->\n<ui-list>\n  <button type=\"button\" class=\"ui-list-item\">\n    <!--任意内容-->\n  </button>\n  <button type=\"button\" class=\"ui-list-item\">\n    <!--任意内容-->\n  </button>\n  <button type=\"button\" class=\"ui-list-item\">\n    <!--任意内容-->\n  </button>\n</ui-list>\n```\n\n### 组件简介\n\n#### ui-list\n\n列表最外层容器。\n\n#### ui-list-item / button[class=ui-list-item]\n\n列表每一项的容器。\n\n#### ui-list-inner\n\n带有底部边线和左侧缩进的列表单项内容容器。\n\n#### ui-list-thumbnail\n\n列表左侧的 icon 或图片的容器。\n\n#### ui-list-sliding\n\n带侧滑功能的列表单项内容容器，和 `ui-list-option` 一起出现，且在同一级。\n\n#### ui-list-option\n\n带侧滑功能的列表单项隐藏按扭容器，和 `ui-list-sliding` 一起出现，且在同一级。\n\n\n"

