module.exports = "import { Component, OnInit } from '@angular/core';\n\n@Component({\n    templateUrl: './refresh-example.component.html',\n    styleUrls: ['./refresh-example.component.scss']\n})\nexport class RefreshExampleComponent implements OnInit {\n    progress: number = 0;\n\n    list: Array<number> = [];\n\n    private index: number = 0;\n\n    ngOnInit() {\n        // 从服务端获取数据\n        setTimeout(() => {\n            for (let i = 0; i < 30; i++) {\n                this.list.push(Math.random());\n                this.index = i;\n            }\n        }, 300);\n    }\n\n    dragging(progress: number) {\n        this.progress = progress;\n    }\n\n    refresh(fn: () => void) {\n        // 用户触发了下拉刷新\n        setTimeout(() => {\n            this.list = [];\n            for (let i = 0; i < 30; i++) {\n                this.list.push(Math.random());\n                this.index = i;\n            }\n            // 刷新完成了\n            fn();\n        }, 3000);\n    }\n\n    infinite(fn: () => void) {\n        // 用户触发了下拉加载，从服务端获取更多数据\n        setTimeout(() => {\n            let min = this.index;\n            let max = min + 30;\n            for (; min < max; min++) {\n                this.list.push(Math.random());\n                this.index = min;\n            }\n            // 加载完成了\n            fn();\n        }, 3000);\n    }\n}"

